<template>

  <div>
    <button
      class="variantlink"
      @click="showModal = !showModal"
      @keyup.esc="showModal = false"
    >

      <span v-if="variants.length>1">{{variants.length}} variants available</span>
      <span v-else>add to cart</span>
    </button>

    <transition
      @enter="startTransitionModal"
      @after-enter="endTransitionModal"
      @before-leave="endTransitionModal"
      @after-leave="startTransitionModal"
    >
      <div
        class="modal fade variant-modal"
        v-if="showModal"
        ref="modal"
        @click.self="showModal = false"
        @keyup.esc="showModal = false"
      >
        <div
          class="modal-dialog modal-dialog-centered modal-dialog-scrollable "
          role="document"
          @keyup.esc="showModal = false"
        >
          <div class="modal-content">
            <div class="modal-header">
              <h5
                class="modal-title"
                id="exampleModalLabel"
              >Product Variants</h5>
              <button
                class="close"
                type="button"
                @click="showModal = false"
              ><span aria-hidden="true">×</span></button>
            </div>
            <div class="modal-body">

              <div
                class="submitted-note"
                v-if="submitted"
              >
                <span v-if="addCount==1">this Item has</span><span v-else>these Items have</span> been added to your cart:
              </div>

              <table
                class="table table-striped"
                :class="{submitted}"
              >
                <tr v-if="!submitted">
                  <th>Product</th>
                  <th>Price</th>
                  <th>Qty</th>
                </tr>
                <tr
                  v-for="v in sortedVariants"
                  :key="v.v_id"
                  v-if="(!submitted || quantities[v.v_id.toString()]>0)"
                >
                  <td v-if="submitted">
                    {{ quantities[v.v_id.toString()]}} x
                  </td>
                  <td>
                    {{ v.title }}
                  </td>

                  <td
                    class="price"
                    v-if="isVatIncl"
                  >
                    <div class="primary">
                      {{ v.price_incl  }}
                    </div>
                    <div class="secondary">
                      excl. VAT {{ v.price  }}
                    </div>
                  </td>

                  <td
                    class="price"
                    v-else
                  >
                    <div class="primary">
                      {{ v.price  }}
                    </div>
                    <div class="secondary">
                      incl. VAT {{ v.price_incl  }}
                    </div>
                  </td>
                  <td
                    class="qty"
                    v-if="!submitted"
                  >
                    <NumberInput
                      v-model="quantities[v.v_id.toString()]"
                      :min="0"
                      :max="100"
                      inline
                      controls
                    ></NumberInput>
                  </td>
                </tr>
              </table>

            </div>
            <div
              class="modal-footer"
              v-if="submitted"
            >
              <button
                class="btn btn-link"
                @click="showModal = false"
              >continue shopping</button>
              <div>&nbsp;</div>
              <a
                href="/cart"
                class="btn btn-primary"
                type="button"
              >go to cart</a>
            </div>
            <div
              class="modal-footer"
              v-else
            >
              <button
                class="btn btn-secondary"
                @click="showModal = false"
              >Close</button>
              <button
                class="btn btn-primary"
                type="button"
                @click="addToCart"
                :disabled="addCount<1"
              >Add to cart</button>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div
      class="modal-backdrop fade d-none"
      ref="backdrop"
      @keyup.esc="showModal = false"
    ></div>
  </div>

  </div>
</template>



<script>
const R = require("ramda");

import NumberInput from "./NumberInput";

export default {
  name: "Variants",
  data: function() {
    return {
      showModal: false,
      quantities: {},
      submitted: false
      // popupOpen: false
    };
  },
  props: {
    variants: { type: Array },
    isVatIncl: { type: Boolean }
  },
  components: {
    NumberInput
  },
  watch: {
    showModal: function(val) {
      if (val) {
        document.body.classList.add("modal-open");
        this.onOpen();
        setTimeout(() => {
          this.setFocus();
        }, 500);
      } else {
        document.body.classList.remove("modal-open");
        this.resetData();
        this.refreshCartCount();
      }
    }
  },
  computed: {
    sortedVariants: function() {
      const byVid = R.ascend(R.prop("v_id"));
      return R.sort(byVid, this.variants);
    },
    addCount: function() {
      return R.pipe(
        R.toPairs(),
        R.reduce((acc, [key, val]) => acc + val, 0)
      )(this.quantities);
    }
  },
  methods: {
    onOpen: function() {
      if (this.variants.length == 1) {
        this.quantities = {
          [this.variants[0].v_id.toString()]: 1
        };
      }
    },
    refreshCartCount: function() {
      this.$store.dispatch("getCartCount");
    },
    resetData: function() {
      Object.assign(this.$data, this.$options.data());
    },
    addToCart: function() {
      this.$store
        .dispatch("addToCart", this.quantities)
        .then(cart => {
          this.submitted = true;
          this.refreshCartCount();
        })
        .catch(error => console.error(`error in addToCart`));
    },
    setFocus: function() {
      this.$el.querySelector("input").focus();
    },
    startTransitionModal() {
      this.$refs.backdrop.classList.toggle("d-block");
      if (this.$refs.modal) {
        this.$refs.modal.classList.toggle("d-block");
      }
    },
    endTransitionModal() {
      this.$refs.backdrop.classList.toggle("show");
      this.$refs.modal.classList.toggle("show");
    }
  },
  created: function() {}
};
</script>

<style lang="scss">
@import "../styles/settings.scss";
.variant-modal {
  .modal-dialog {
    max-width: 800px;
    // margin-left: 1rem;
    // margin-right: 1em
    border: 0.5rem solid transparent;
  }
  .qty {
    max-width: 9rem;
  }
  .submitted-note {
    background: #777;
    color: white;
    text-align: center;
    font-size: 1.1em;
    padding: 1rem;
  }
}
</style>
