<template>

  <div>

    <a
      href="/cart/"
      title="view cart"
      class="cart-icon"
    >

      <span class="p1 fa-stack fa-2x fa-lg has-badge">
        <i class="fas fa-shopping-cart fa-stack-1x"></i>
        <span class="cart-count">{{cartCount}}</span>
      </span>

    </a>
  </div>

</template>


<script>
// const R = require("ramda");

export default {
  name: "CartIcon",
  computed: {
    cartCount: function() {
      return this.$store.getters.cartCount;
    }
  },
  methods: {
    refreshCartCount: function() {
      this.$store.dispatch("getCartCount");
    }
  },
  created: function() {
    this.refreshCartCount();
  }
};
</script>

