<template>
  <div>
    <div class="prod-list">
      <div
        v-for="(i, index) in grouped_products"
        :key="index"
        class="prod-item"
      >
        <div class="inner">
          <div
            class="imgpane"
            :style="{backgroundImage:  makeUrl(i.img_url)}"
          >
          </div>
          {{i.title}}

          <div class="price">
            <template v-if="isVatIncl">
              <div class="primary">
                {{i.price_incl}}
              </div>
              <div class="secondary">
                excl. VAT {{i.price}}
              </div>
            </template>
            <template v-else>
              <div class="primary">
                {{i.price}}
              </div>
              <div class="secondary">
                incl. VAT{{i.price_incl}}
              </div>
            </template>
          </div>

          <VariantsModal
            :variants="i.variants"
            :isVatIncl="isVatIncl"
            key="i.v_id"
          ></VariantsModal>
        </div>
      </div>

    </div>
  </div>
  </div>
  </div>
</template>

<script>
const R = require("ramda");

import VariantsModal from "./VariantsModal";

export default {
  name: "Prodlist",
  data() {
    return {
      // popupOpen: false
    };
  },
  components: {
    VariantsModal
  },
  props: {
    childNodes: { type: Array },
    currentProductId: { type: String },
    vatType: { type: String, default: "incl" }
  },
  methods: {
    makeUrl: function(url) {
      return `url('${url}')`;
    },
    setCurrentProductId: function(id) {
      this.$store.commit("setCurrentProductId", id);
      this.popupOpen = false;
    },
    togglePopup: function() {
      console.log("#log 5226", this.popupOpen);
      this.popupOpen = !this.popupOpen;
    },
    addToCart: function(sku) {
      this.$store.dispatch("addToCart", sku);
    }
  },
  computed: {
    isVatIncl: function() {
      return this.vatType == "incl";
    },
    popupVisible: function(re) {
      if (this.usePopup) {
        return this.popupOpen;
      }
      return true;
    },
    usePopup: function() {
      return false;
      // return R.length(this.products || []) > 10;
    },
    products: function() {
      console.log(`#log 8276`, this);
      return R.map(item => {
        return {
          ...R.omit(["class"], item)
        };
      })(this.childNodes);
    },
    grouped_products: function() {
      let items = R.pipe(
        R.groupBy(R.prop("id")),
        R.toPairs(),
        R.map(([key, variants]) => {
          return { ...variants[0], variants };
        })
      )(this.products);

      return items;
    },
    listedProducts: function() {
      if (this.usePopup) {
        const currentProductId = this.$store.getters.currentProductId;
        return R.filter(a => a.id !== currentProductId)(this.products);
      }
      return this.products;
    },
    currentProduct: function() {
      return this.$store.getters.currentProduct;
    }
  },
  created: function() {
    this.$store.commit(
      "setCurrentProductId",
      parseInt(this.currentProductId, 10)
    );

    this.$store.commit("setProducts", [...this.products]);
  }
};
</script>
